<script>
  import VueMomentsAgo from "@/components/VueMoments.vue";
  import FeedbackUp from "@/js/feedbackup/feedbackup";
  import Avatar from "vue-avatar";

  export default {
    components: { VueMomentsAgo, Avatar },
    data() {
      return {
        defaultPic: "https://infinity.500apps.com/img/default_avatar.png",
        loading: true,
        feedback_loading: false,
        activity_data: [],
        feedbackData: [],
        feedback_id: window.fid,
      };
    },
    async mounted() {
      this.feedback_loading = false;
      await this.getFeedback();
      await this.getFeedbackActivity();
    },
    destroyed() {},
    methods: {
      async getFeedback() {
        this.feedback_loading = false;
        try {
          let response = await FeedbackUp.getDetails(this.feedback_id, "feedback");
          this.feedbackData = response[0];
          this.feedback_loading = true;
        } catch (e) {
          this.feedback_loading = true;
          this.feedbackData = [];
        }
      },
      async getFeedbackActivity() {
        try {
          this.loading = true;
          let response = await FeedbackUp.getFeedbackDetails(this.feedback_id);
          this.loading = false;
          this.activity_data = response;
        } catch (e) {
          this.loading = false;
          this.activity_data = [];
        }
      },
    },
  };
</script>
